import Link from 'next/link'
import { users } from '../../../../user-data'

export default async function XXX({ params }: { params: { id: string } }) {
  const { id } = await params
  const user = users.find((user) => user.id === id)
  if (!user) {
    return <div>User not found</div>
  }
  return (
    <div className=' m-5 p-5 rounded-lg max-w-2xs  dark:text-white bg-[#f5f6fb] dark:bg-[#0c1017]'>
      <Link href="/users" className='m-5'>Back to users</Link>
      <form className="flex flex-col gap-10 m-5">
        <input  type="text" name="name" defaultValue={user.name} />
        <input type="text" name="phoneNumber" defaultValue={user.phoneNumber} />
        <button type="submit" className='bg-blue-500 rounded-xl p-2 max-w-12 mx-auto'>Save</button>
      </form>
    </div>
  )
}
